const createUnit = (
    unitName,                   // string
    unitHealth,                 // integer
    unitMana,                   // integer
    unitHRege,                  // integer
    unitMRege,                  // integer
    unitAtk,                    // integer
    unitDef,                    // integer
    unitDmg,                    // integer
    unitAtkMethod,              // [[attackType(string), minRange(integer), maxRange(integer), damageType(integer)], ...]
    unitMov,                    // integer
    unitInit,                   // integer
    unitMor,                    // integer
    unitCri,                    // integer
    unitSize,                   // integer
    unitCostM,                  // [integer, integer, integer]
    unitCostMM,                 // [integer, integer, integer]
    unitCostT,                  // integer
    unitPop,                    // integer
    unitReq,                    // [string, ...]
    unitAbili,                  // {title(string):content(string), ...}
    unitDesc,                   // string
    unitImg,                    // [ImageURL, ImageIconURL]
) => (
    {
        name: unitName,
        health: unitHealth,
        mana: unitMana,
        healthRegenerate: unitHRege,
        manaRegenerate: unitMRege,
        atk: unitAtk,
        def: unitDef,
        dmg: unitDmg,
        attackMethod: unitAtkMethod,
        mov: unitMov,
        init: unitInit,
        mor: unitMor,
        cri: unitCri,
        size: unitSize,
        costMaterial: unitCostM,
        costMaintain: unitCostMM,
        costTime: unitCostT,
        population: unitPop,
        requirement: unitReq,
        abilities: unitAbili,
        description: unitDesc,
        imageUrl: unitImg,
    }
)

const createBuilding = (
    bdName,                     // string
    bdHealth,                   // integer
    bdMana,                     // integer
    bdHRege,                    // integer
    bdMRege,                    // integer
    bdAtk,                      // integer
    bdDef,                      // integer
    bdDmg,                      // integer
    bdAtkMethod,                // [[attackType(string), minRange(integer), maxRange(integer), damageType(integer)], ...]
    bdSize,                     // integer
    bdCostM,                    // [integer, integer, integer]
    bdCostR,                    // [integer, integer, integer, health recover]
    bdCostT,                    // integer
    bdPop,                      // [integer, integer]/
    bdReq,                      // [string, ...]
    bdAbili,                    // {title(string):content(string), ...}
    bdTech,                     // {title(string):[cost1(integer),cost2(integer),cost3(integer),costTime(intger),requirement([string,...]),content(string)], ...}
    bdDesc,                     // string
    bdImg,                      // [ImageURL, null]
) => (
    {
        name: bdName,
        health: bdHealth,
        mana: bdMana,
        healthRegenerate: bdHRege,
        manaRegenerate: bdMRege,
        atk: bdAtk,
        def: bdDef,
        dmg: bdDmg,
        attackMethod: bdAtkMethod,
        size: bdSize,
        costMaterial: bdCostM,
        costRepair: bdCostR,
        costBuildTime: bdCostT,
        population: bdPop,
        requirement: bdReq,
        abilities: bdAbili,
        technologies: bdTech,
        description: bdDesc,
        imageUrl: bdImg,
    }
)

const createHero = (
    hrName,                     // string
    hrSubtitle,                 // string
    hrHealth,                   // [integer, integer, integer, integer]
    hrMana,                     // [integer, integer, integer, integer]
    hrHRege,                    // [integer, integer, integer, integer]
    hrMRege,                    // [integer, integer, integer, integer]
    hrAtk,                      // [integer, integer, integer, integer]
    hrDef,                      // [integer, integer, integer, integer]
    hrDmg,                      // [integer, integer, integer, integer]
    hrAtkMethod,                // [[attackType(string), minRange(integer), maxRange(integer), damageType(integer)], ...]
    hrMov,                    // [integer, integer, integer, integer]
    hrInit,                   // [integer, integer, integer, integer]
    hrMor,                    // [integer, integer, integer, integer]
    hrCri,                    // [integer, integer, integer, integer]
    hrSize,                   // string
    hrAbili1,                 // {title(string):[name(string),content(string)], ...}
    hrDesc,                   // string
    hrImg,                    // [ImageURL, ImageIconURL]
) => (
    {
        name: hrName,
        subtitle: hrSubtitle,
        health: hrHealth,
        mana: hrMana,
        healthRegenerate: hrHRege,
        manaRegenerate: hrMRege,
        atk: hrAtk,
        def: hrDef,
        dmg: hrDmg,
        attackMethod: hrAtkMethod,
        mov: hrMov,
        init: hrInit,
        mor: hrMor,
        cri: hrCri,
        size: hrSize,
        abilities: hrAbili1,
        description: hrDesc,
        imageUrl: hrImg,
    }
)


export { createUnit, createBuilding, createHero }